import { Controller, Node } from '@ali/4ever-cangjie';
import RefBlock from '../../mo/models';
import type { BiConfig } from '../../utils/types';
import { isSyncHetuNode } from '../../utils/logic';

export default function createDisableNodeInRefBlock(
  disableNestedInRefBlock: BiConfig['disableNestedInRefBlock'],
) {
  /**
   * 禁止某种节点存在于引用块
   * @param controller
   * @param node
   * @returns
   */
  return function disableNodeInRefBlock(controller: Controller, node: Node) {
    return (
      RefBlock.isRefBlock(node) ||
      isSyncHetuNode(node) ||
      disableNestedInRefBlock!(node, controller)
    );
  };
}
